import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { MyEnquiryListPage } from './my-enquiry-list.page';
import { MyEnquiriesResolver } from '@feature/my-enquiries/resolvers/my-enquiries-resolver';

const routes: Routes = [
	{
		path: '',
		component: MyEnquiryListPage,
		resolve: { enquiryList: MyEnquiriesResolver },
		runGuardsAndResolvers: 'paramsOrQueryParamsChange',
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class MyEnquiryListPageRoutingModule {}
