import { Component, Input, OnInit } from '@angular/core';
import { AdStatus, AisleType, BooleanStatus } from '@core/enums';
import { Inquiry } from '@core/models';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-my-enquiry-item',
    templateUrl: './my-enquiry-item.component.html',
    styleUrls: ['./my-enquiry-item.component.scss'],
    standalone: false
})
export class MyEnquiryItemComponent implements OnInit {
	@Input() enquiry: Inquiry;

	readonly buttonType = ButtonType;
	readonly booleanStatus = BooleanStatus;
	readonly aisleType = AisleType;
	readonly adStatusEnum = AdStatus;

	canShowTenantStatus = false;

	ngOnInit() {
		this.canShowTenantStatus =
			this.enquiry.ad.aisle.reference !== AisleType.RENT_COMMERCIAL &&
			this.enquiry.ad.aisle.reference !== AisleType.SALE_COMMERCIAL &&
			this.enquiry.ad.aisle.reference !== AisleType.SALE_RESIDENTIAL;
	}
}
