import { Component } from '@angular/core';
import { MyEnquiriesFacade } from '@feature/my-enquiries/my-enquiries.facade';

@Component({
    selector: 'findqo-my-enquiry-list',
    templateUrl: './my-enquiry-list.page.html',
    styleUrls: ['./my-enquiry-list.page.scss'],
    standalone: false
})
export class MyEnquiryListPage {
	constructor(private myEnquiriesFacade: MyEnquiriesFacade) {}

	enquiryList$ = this.myEnquiriesFacade.getEnquiryList$();
	pageMetaData$ = this.myEnquiriesFacade.getEnquiryListMetaData$();
	isLoading$ = this.myEnquiriesFacade.getIsLoading$();
}
