import { inject } from '@angular/core';
import { ResolveFn, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { Observable, catchError, tap } from 'rxjs';
import { Inquiry, PageMetaData } from '@core/models';
import { HttpErrorResponse } from '@angular/common/http';
import { APP_ROUTE } from '@core/constants';
import { MyEnquiriesFacade } from '../my-enquiries.facade';

export const MyEnquiriesResolver: ResolveFn<any> = (
	route: ActivatedRouteSnapshot,
	state: RouterStateSnapshot,
	router: Router = inject(Router),
	adsApi: AdsApi = inject(AdsApi),
	myEnquiriesFacade: MyEnquiriesFacade = inject(MyEnquiriesFacade)
): Observable<{ data: Inquiry[]; meta: PageMetaData }> => {
	const PAGE = route.queryParams['page'] ? +route.queryParams['page'] : 1;

	return adsApi.getInquiryList({ page: PAGE, perPage: 20 }).pipe(
		catchError((response: HttpErrorResponse) => {
			if (response?.status === 400 || response?.status === 500) {
				router.navigate([APP_ROUTE.wildCard], { skipLocationChange: true });
			}

			throw new Error(response?.message);
		}),
		tap((response: { data: Inquiry[]; meta: PageMetaData }) => {
			myEnquiriesFacade.setEnquiryList(response?.data);
			myEnquiriesFacade.setEnquiryListMetaData(response?.meta);
		})
	);
};
